import React from 'react';

import { Link } from 'react-router-dom';

import './EventsBar.css';

export default class EventsBar extends React.Component{

    render(){
        const { selectedEvent } = this.props;

        return(
            <>
                {
                    selectedEvent && 
                        (
                            <Link 
                                className={selectedEvent ? 'vision events' : 'events'} 
                                to={`/app/event/id_${selectedEvent.id}`}
                            >
                                <div className="events__picture">
                                    <img src={selectedEvent.image} alt="Images" className="events__picture-img"/>
                                    <div className="events__block-head">
                                        <div className="events__block-data">
                                            {selectedEvent.data}
                                        </div>
                                        <div className="events__block-time">
                                            {selectedEvent.time}
                                        </div>
                                    </div>
                                </div>
                                <div className="events__block-info">
                                    <div className="events__block-title">{selectedEvent.title}</div>
                                </div>
                            </Link>
                        )
                }
            </>
        )
    }
}