import React from 'react';
import { Link } from 'react-router-dom';

import './EventsAll.css';
import '../EventsBar/EventsBar.css';

import PrevAndOptionsBar from '../PrevAndOptionsBar';



import event_1 from '../../assets/events/1.jpg';
import event_2 from '../../assets/events/2.jpg';
import event_3 from '../../assets/events/3.jpg';

export default class EventsAll extends React.Component{
    state = {
        events:[
            {
                id:1,
                lat:  47.0964,
                lon: 37.543,
                title:'Концерт Thirty Seconds to Mars',
                data: '23 июля',
                time: '20:00',
                info:'Описание социального события',
                image: event_1,
                access:'По билетам'
            },
            {
                id:2,
                lat:  47.0964,
                lon: 37.541,
                title:'Концерт Imagine Dragons',
                data: '22 июля',
                time: '19:00',
                info:'Описание социального события',
                image: event_2,
                access:'По билетам'
            },
            {
                id:3,
                lat:  47.0964,
                lon: 37.546,
                title:'Концерт Дня Металургов',
                data: '26 июля',
                time: '19:00',
                info:'Описание социального события',
                image: event_3,
                access:'Бессплатно'
            },
        ]
    }

    render(){
        const { events } = this.state;

        return(
            <div className="events-all">
                <PrevAndOptionsBar />
                <ul className="events-all__list">
                    {events.map(event=>{
                        return(
                            <li className="events-all__item" key={event.id}>
                                <Link 
                                    className="events-all__link"
                                    to={`/app/event/id_${event.id}`}
                                >
                                    <div className="events__picture">
                                        <img src={event.image} alt="Images" className="events__picture-img"/>
                                        <div className="events__block-head">
                                            <div className="events__block-data">
                                                {event.data}
                                            </div>
                                            <div className="events__block-time">
                                                {event.time}
                                            </div>
                                        </div>
                                    </div>
                                    <div className="events__block-info">
                                        <div className="events__block-title">{event.title}</div>
                                    </div>
                                </Link>
                            </li>
                        )
                    })}
                </ul>
            </div>
        )
    }
}