import React from 'react';
import ReactMapGL, { Marker, Popup } from 'react-map-gl';
import './Home.css';

import EventsBar from '../EventsBar';
import MenuBar from '../MenuBar';

import event_1 from '../../assets/events/1.jpg';
import event_2 from '../../assets/events/2.jpg';
import event_3 from '../../assets/events/3.jpg';
  
export default class Map extends React.Component{
    state = {
        viewport: {
            latitude:  47.0964,
            longitude: 37.543,
            width: "100%",
            height: "100%",
            zoom: 15,
        },
        marks: [
            {
                id:1,
                lat:  47.0964,
                lon: 37.543,
                title:'Концерт Thirty Seconds to Mars',
                data: '23 июля',
                time: '20:00',
                info:'Описание социального события',
                image: event_1,
                access:'По билетам'
            },
            {
                id:2,
                lat:  47.0964,
                lon: 37.541,
                title:'Концерт Imagine Dragons',
                data: '22 июля',
                time: '19:00',
                info:'Описание социального события',
                image: event_2,
                access:'По билетам'
            },
            {
                id:3,
                lat:  47.0964,
                lon: 37.546,
                title:'Концерт Дня Металургов',
                data: '26 июля',
                time: '19:00',
                info:'Описание социального события',
                image: event_3,
                access:'Бессплатно'
            },
        ],
        selectedEvent: null,
    };

    setViewport = (viewport) =>{
        this.setState({viewport})
    }

    setSelectedEvent = (marker) =>{
        this.setState({
            selectedEvent : marker,
        })
    }

    render(){
        return(
            <>
                <ReactMapGL
                    mapboxApiAccessToken={process.env.REACT_APP_MAPBOX_KEY}
                    onViewportChange={this.setViewport}
                    {...this.state.viewport}
                >
                    {this.state.marks.map(mark=>{
                        return(
                            <Marker
                                key={mark.id}
                                latitude={mark.lat}
                                longitude={mark.lon}
                            >
                                <button 
                                    className="map__button"
                                    onClick={e=>{
                                        e.preventDefault()
                                        this.setSelectedEvent(mark)
                                    }}
                                >
                                    <span className="mgo-widget-call_pulse"></span>
                                </button>
                            </Marker>
                        )
                    })}

                    {this.state.selectedEvent 
                        ?
                            (
                                <Popup
                                    latitude={this.state.selectedEvent.lat}
                                    longitude={this.state.selectedEvent.lon}
                                    onClose={e=>{
                                        this.setSelectedEvent(null)
                                    }}
                                >
                                    <div className="map__popup">
                                        <div className="map__popup-title">
                                            {this.state.selectedEvent.title}
                                        </div>
                                        <div className="map__popup-access">
                                            {`Вход: ${this.state.selectedEvent.access}`}
                                        </div>
                                    </div>
                                </Popup>
                            )
                        : 
                                null
                    }
                </ReactMapGL>

                <EventsBar selectedEvent={this.state.selectedEvent} />
                <MenuBar />
            </>
        )
    }
}

