import React from 'react';
import { Redirect } from 'react-router-dom';
import './Login.css';

import { withAuth } from '../../context/Auth';

class Login extends React.Component{
    state = {
        email:'',
        password:'',
    }

    handleChangeInput = (e) =>{
        this.setState({
            [e.target.name] : e.target.value,
        })
    }

    onSubmitForm = (e) =>{
        const { email, password } = this.state;
        const { authorize  } = this.props;

        e.preventDefault();
        
        authorize(email, password);
    }

    render(){

        const { isAuthorized } = this.props;
    
        return(
            isAuthorized ? (
                <Redirect to="/app" />
            ) : (
                <div className="login">
                    <div className="login__wrapp">
                        <div className="login__form">
                            <form className="form" onSubmit={this.onSubmitForm}>
                                <label className="form__label">
                                    <input 
                                        type="email" 
                                        name="email"
                                        className="form__input" placeholder="email" 
                                        value={this.state.email}
                                        onChange={this.handleChangeInput}
                                    />
                                </label>
                                <label className="form__label mb-none">
                                    <input 
                                        type="password"
                                        name="password" 
                                        className="form__input" placeholder="password" 
                                        value={this.state.password}
                                        onChange={this.handleChangeInput}
                                    />
                                </label>
                                <div className="form__button">
                                    <button type="button" className="button button--under">Forgot password ?</button>
                                    <button type="submit" className="button button--submit" onClick={this.onSubmitForm}>Login</button>
                                </div>
                            </form>
                        </div>
                        <div className="login__create">
                            <div className="login__create-text">
                                Don't have an account?
                            </div>
                            <button type="button" className="button button--under">Sign up now</button>
                        </div>
                    </div>
                </div>
            )
        )
    }
}

export default withAuth(Login);