import React from 'react';
import { Link } from 'react-router-dom';
import './MenuBar.css';

export default class MenuBar extends React.Component{
    state = {
        menu:[
            {
                name:'Profile',
                to:'/app/profile'
            },
            {
                name:'Events',
                to:'/app/events'
            },
        ] 
    }
    render(){
        return (
            <div className="menu-bar">
                <ul className="menu-bar__list">
                    {
                        this.state.menu.map((el,index)=>{
                            return(
                               <li className="menu-bar__item" key={`${index}`}>
                                    <Link to={el.to} className="menu-bar__link">
                                        {el.name}
                                    </Link>
                               </li>
                            )
                        })
                    } 
                </ul>
            </div>
        )           
    }     
}