import React from 'react';
import { Link } from 'react-router-dom';

import ButtonOptions from '../ButtonOptions';

import left_arrow from '../../assets/back.png';

export default class PrevAndOptionsBar extends React.Component{

    render(){
        return(
            <>
                <div className="options-bar options-bar__prev prev">
                    <Link to="/app" className="prev__button">
                        <img src={left_arrow} alt="Icon" className="prev__button-img"/>
                    </Link>
                    <ButtonOptions />
                </div>
            </>
        )
    }

    
}


