import React, { useState } from 'react';

import three_dots from '../../assets/options.png';

const ButtonOptions = () =>{
    const [ isActive , setIsActive ] = useState(false);

        return(
            <>
                <button 
                    className="options__button" 
                    onClick={e=>{
                        setIsActive(!isActive)
                    }}
                >
                    <img src={three_dots} alt="Icon" className="options__button-img"/>
                    {
                        isActive 
                            ?
                                <ul className="options__list">
                                    <li className="options__item" onClick={e=>setIsActive(!isActive)}>
                                        Пункт опций
                                    </li>
                                </ul>
                            :
                                null
                    }
                </button>
            </>
        )
}
export default ButtonOptions;