import React from 'react';

import './ProfilePage.css';
import '../EventsBar/EventsBar.css';

import EventsAll from '../EventsAll';


import user_avatar from '../../assets/user.jpg';

export default class ProfilePage extends React.Component{
    state = {
        user:{
            name:'Nick',
            surname: 'Momotenko',
            city:'New York',
            avatar: user_avatar,
            followers: 50,
            following: 10,
            events: 2,
        }
    }

    render(){
        const { user } = this.state;

        return(
            <div className="profile-page">
                <div className="container">
                    <div className="profile-page__account">
                        <div className="profile-page__header" style={{background:`url(${user_avatar}) center / cover`}}>
                            <div className="profile-page__row">
                                <div className="profile-page__personal">
                                    {`${user.name} ${user.surname}`}
                                </div>
                                <div className="profile-page__city">
                                    {`${user.city}`}
                                </div>
                            </div>
                            <div className="profile-page__row">
                                <div className="profile-page__info">
                                    <div className="profile-page__col">
                                        <div className="profile-page__followers">
                                            {user.followers}
                                        </div>
                                        <div className="profile-page__subtitle">Followers</div>
                                    </div>
                                    <div className="profile-page__col">
                                        <div className="profile-page__following">
                                            {user.following}
                                        </div>
                                        <div className="profile-page__subtitle">Following</div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="profile-page__avatar">
                            <div className="profile-page__avatar-icon">
                                <img src={user_avatar} alt="User Avatar" className="profile-page__avatar-icon_picture"/>
                            </div>
                            <div className="profile-page__button">
                                <button className="button button--follow">Follow</button>
                            </div>
                        </div>
                        <div className="profile-page__content">
                            <div className="profile-page__title">Active Events</div>
                            <div className="profile-page__descr">
                                You can attend active events and enjoy their atmosphere
                            </div>
                            <EventsAll />
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}