import React from 'react';
import { Route , Link } from 'react-router-dom';

import './EventPage.css';

import ButtonOptions from '../ButtonOptions';

import left_arrow from '../../assets/back.png';

import event_1 from '../../assets/events/1.jpg';
import event_2 from '../../assets/events/2.jpg';
import event_3 from '../../assets/events/3.jpg';

export default class EventPage extends React.Component{
    state = {
        events:[
            {
                id:1,
                lat:  47.0964,
                lon: 37.543,
                title:'Концерт Thirty Seconds to Mars',
                data: '23 июля',
                time: '20:00',
                info:'Описание социального события',
                image: event_1,
                access:'По билетам'
            },
            {
                id:2,
                lat:  47.0964,
                lon: 37.541,
                title:'Концерт Imagine Dragons',
                data: '22 июля',
                time: '19:00',
                info:'Описание социального события',
                image: event_2,
                access:'По билетам'
            },
            {
                id:3,
                lat:  47.0964,
                lon: 37.546,
                title:'Концерт Дня Металургов',
                data: '26 июля',
                time: '19:00',
                info:'Описание социального события',
                image: event_3,
                access:'Бессплатно'
            },
        ]
    }

    render(){
        const { events } = this.state;
        
        return(
            <>
                {events.map(el=>{
                    return <Route exact path={`/app/event/id_${el.id}`} key={el.id} render={(props)=> <EventPageInfo {...this.props} event={el} />}/>
                })}
            </>
        )
    }
}

const EventPageInfo = (props) =>{
    const { event } = props;

    return(
        <div className="event-page">
            <div className="event-page__head">
                <div className="event-page__prev prev">
                    <Link to="/app" className="prev__button">
                        <img src={left_arrow} alt="Icon" className="prev__button-img"/>
                    </Link>
                </div>
                <div className="event-page__options options">
                    <ButtonOptions />
                </div>
            </div>
            <div className="event-page__content">
                <div className="event-page__image">
                    <img src={event.image} alt="Avatar" className="event-page__image-pic"/>
                </div>
                <div className="event-page__access">
                    <div className="event-page__access-name">
                        Вход : 
                    </div>
                    <div className="event-page__access-value">
                         {event.access}
                    </div>
                </div>
                <div className="event-page__title">{event.title}</div>
                <div className="event-page__bar">
                    <div className="event-page__bar-date">{event.data}</div>
                    <div className="event-page__bar-time">{event.time}</div>
                </div>
                <div className="event-page__info">
                    {event.info}
                </div>
                {
                    event.access === 'По билетам'
                        ?
                            <div className="event-page__buy-ticket">
                                <button className="button button__buy-ticket">Купить билет</button>
                            </div>
                        :
                            null
                }
            </div>
        </div>
    )
}

